<template lang="pug">
#project-modal
  .modal-content
    .close(@click="closeModal")
      svg.bi.bi-x(
        width="32px",
        height="32px",
        viewBox="0 0 16 16",
        fill="#c7e4e9",
        xmlns="http://www.w3.org/2000/svg"
      )
        path(
          fill-rule="evenodd",
          d="M4.646 4.646a.5.5 0 0 1 .708 0L8 7.293l2.646-2.647a.5.5 0 0 1 .708.708L8.707 8l2.647 2.646a.5.5 0 0 1-.708.708L8 8.707l-2.646 2.647a.5.5 0 0 1-.708-.708L7.293 8 4.646 5.354a.5.5 0 0 1 0-.708z"
        )
    .gif
      img(:src="selectedProject.gif") 
    .project-content
      .top-row-content
        .project-name
          p {{ selectedProject.projectName }}
        .restricted-site-cta(v-if="selectedProject.links.length === 0")
          svg.bi.bi-slash-circle-fill(
            width="16px",
            height="16px",
            viewBox="0 0 16 16",
            fill="#adadad",
            xmlns="http://www.w3.org/2000/svg"
          )
            path(
              fill-rule="evenodd",
              d="M16 8A8 8 0 1 1 0 8a8 8 0 0 1 16 0zm-4.646-2.646a.5.5 0 0 0-.708-.708l-6 6a.5.5 0 0 0 .708.708l6-6z"
            )
          p Restricted Site

        .visit-site-cta(
          v-if="selectedProject.links.length > 1",
          @click="toggleDropDown"
        )
          .dropdown(v-if="isShowDropDown")
            .dropdown-link(
              v-for="(link, index) in selectedProject.links",
              :key="'dropdown-link-' + index",
              :class="{ 'dropdown-link-end': index === selectedProject.links.length - 1 }",
              @click="goTo(link)"
            )
              p {{ selectedProject.linkLabels[index] }}
          svg.bi.bi-chevron-down(
            width="16px",
            height="16px",
            viewBox="0 0 16 16",
            fill="#fff",
            xmlns="http://www.w3.org/2000/svg",
            v-if="selectedProject.links.length > 1"
          )
            path(
              fill-rule="evenodd",
              d="M1.646 4.646a.5.5 0 0 1 .708 0L8 10.293l5.646-5.647a.5.5 0 0 1 .708.708l-6 6a.5.5 0 0 1-.708 0l-6-6a.5.5 0 0 1 0-.708z"
            )
          p Visit Site

        .visit-site-cta(
          v-if="selectedProject.links.length === 1",
          @click="goTo(selectedProject.links[0])"
        ) 
          svg.bi.bi-box-arrow-up-right(
            width="16px",
            height="16px",
            viewBox="0 0 16 16",
            fill="#fff",
            xmlns="http://www.w3.org/2000/svg",
            v-if="selectedProject.links.length === 1"
          )
            path(
              fill-rule="evenodd",
              d="M8.636 3.5a.5.5 0 0 0-.5-.5H1.5A1.5 1.5 0 0 0 0 4.5v10A1.5 1.5 0 0 0 1.5 16h10a1.5 1.5 0 0 0 1.5-1.5V7.864a.5.5 0 0 0-1 0V14.5a.5.5 0 0 1-.5.5h-10a.5.5 0 0 1-.5-.5v-10a.5.5 0 0 1 .5-.5h6.636a.5.5 0 0 0 .5-.5z"
            )
            path(
              fill-rule="evenodd",
              d="M16 .5a.5.5 0 0 0-.5-.5h-5a.5.5 0 0 0 0 1h3.793L6.146 9.146a.5.5 0 1 0 .708.708L15 1.707V5.5a.5.5 0 0 0 1 0v-5z"
            )
          p Visit Site
      .divider
      .about 
        //- p {{ selectedProject.description }}
        p #[span(v-html="selectedProject.description")]
      .motivation(v-if="selectedProject.motivation")
        p.label Motivation
        p
          | {{ selectedProject.motivation }}
      .responsibilities(v-if="selectedProject.responsibilities")
        p.label Responsibilities:
        p(
          v-for="(resp, index) in selectedProject.responsibilities",
          :key="'resp-' + index"
        ) 
          |#[span(v-html="resp")]
      .tech-stack
        p.label Tech Stack
        .tech-stack-items
          .tech-item(
            v-for="(tech, index) in selectedProject.techStack",
            :key="'tech-' + index"
          )
            p {{ tech }}
      //- .links
      //-   p.label Links to the site:
      //-   p(v-if="selectedProject.links.length <= 0") Unfortunately, this site is not accessible by the public :(
      //-   .link(
      //-     v-for="(link, index) in selectedProject.links",
      //-     :key="'link-' + index"
      //-   )
      //-     a(:href="link", target="_blank")
      //-       | {{ link }}
</template>

<script>
export default {
  data() {
    return {
      isShowDropDown: false,
    };
  },
  computed: {
    selectedProject() {
      return this.$store.state.selectedProject;
    },
  },
  methods: {
    closeModal() {
      this.$store.commit("setShowProjectModal", false);
    },
    goTo(link) {
      const a = document.createElement("a");
      a.href = link;
      a.target = "_blank";
      a.click();
    },
    toggleDropDown() {
      this.isShowDropDown = !this.isShowDropDown;
    },
  },
};
</script>

<style lang="sass" scoped>
@import '../assets/sass/variables.sass'
#project-modal
  position: fixed
  width: 100vw
  height: 100vh
  z-index: 1000
  background-color: rgba(0, 0, 0, 0.3)
  top: 0%
  left: 0%

.modal-content
  height: 80%
  width: 700px
  max-width: 1024px
  position: absolute
  // background-color: #2b7b8a
  background-color: #c7e4e9
  top: 50%
  left: 50%
  transform: translate(-50%, -50%)
  box-shadow: 0 0 30px 1px #000
  +mobile
    width: 100%
    height: 100%
  p
    // color: #c7e4e9
    color: #1f5a65
  .close
    position: fixed
    right: 20px
    top: 20px
    width: 32px
    height: 32px
    cursor: pointer
    background-color: black
    z-index: 100
    border: 3px solid #c7e4e9
  .gif
    position: relative
    // padding: 20px 0px
    width: 100%
    height: 50%
    background-color: #000
    display: flex
    justify-content: center
    align-items: center
    img
      max-width: 100%
      max-height: 100%
  .project-content
    height: 50%
    position: relative
    overflow: auto
    .top-row-content
      width: 90%
      display: flex
      margin-left: 20px
      margin-bottom: 10px
      align-items: flex-end
      +mobile
        flex-direction: column
        align-items: flex-start
      .project-name
        flex-grow: 1
        padding: 35px 20px 0px 0px
        text-align: left
        font-size: 35px
        line-height: 40px
        font-weight: bold
        +mobile
          margin-bottom: 10px
        p
          color: #1f5a65
      .visit-site-cta
        height: 40px
        width: 130px
        display: flex
        justify-content: center
        align-items: center
        background-color: #e31b6d
        cursor: pointer
        position: relative
        .dropdown
          width: 100%
          position: absolute
          top: 100%
          background-color: #fff
          .dropdown-link
            width: 100%
            text-align: center
            box-sizing: border-box
            border-right: 1px solid #000
            border-left: 1px solid #000
            p
              color: #000
              padding: 10px 0px
          .dropdown-link-end
            border-bottom: 1px solid #000
          .dropdown-link:hover
            background-color: #c7e4e9
            // background-color: #1f5a65
            // p
            //   color: #c7e4e9
        svg
          margin-right: 10px
          margin-left: 20px
        p
          color: #fff
          padding-right: 20px
      .restricted-site-cta
        height: 40px
        display: flex
        justify-content: center
        align-items: center
        // background-color: #787878
        background-color: #e31b6d
        user-select: none
        svg
          margin-right: 10px
          margin-left: 20px
        p
          // color: #1f5a65
          color: #adadad
          padding-right: 20px
    .divider
      width: 90%
      background-color: #1f5a65
      height: 1px
      margin: 0px 20px
    .about
      padding: 20px 20px 0px
      text-align: left
    .motivation
      margin-top: 20px
      padding: 0px 20px
      text-align: left
    .responsibilities
      margin-top: 20px
      padding: 0px 20px
      text-align: left
    .tech-stack
      margin-top: 20px
      padding: 0px 20px 35px
      .tech-stack-items
        display: flex
        justify-content: center
        align-items: center
        .tech-item
          border-radius: 20px
          background-color: #6EB9C6
          padding: 5px 10px
          margin: 0px 10px
          p
            color: #fff
            font-size: 14px
    .links
      margin-top: 20px
      padding: 0px 20px 20px
      text-align: left
      .link
        a
          color: #1f5a65
          cursor: pointer
.label
  font-weight: bold
  padding-bottom: 10px

/* width */
::-webkit-scrollbar
  width: 5px

/* Track */
::-webkit-scrollbar-track
  background: #c7e4e9
  border-radius: 20px
/* Handle */
::-webkit-scrollbar-thumb
  background: #1f5a65
  border-radius: 20px
</style>
